import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { users } from '../users';
import './Login.css';

const Login = ({ setUser, setSelectedBlocks }) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [isRegistering, setIsRegistering] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const savedUser = localStorage.getItem('user');
    if (savedUser) {
      setUser(JSON.parse(savedUser));
      navigate('/home');
    }
  }, [setUser, navigate]);

  const handleLogin = async () => {
    const user = users.find(u => u.username === username && u.password === password);
    if (user) {
      setUser(user);
      localStorage.setItem('user', JSON.stringify(user.username));
      navigate('/home');
    } else {
      try {
        const payload = { username, password };
        const response = await fetch('https://backend-mover.com/mover/login', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(payload),
        });

        if (response.ok) {
          const token = await response.text();
          console.log(token);
          localStorage.setItem('token', token);
          const user = { username, password };
          setUser(user);
          localStorage.setItem('user', JSON.stringify(payload.username)); 
          alert('Login successful');

          // Fetch selected blocks after login
          const blocksResponse = await fetch('https://backend-mover.com/mover/savedBlocks', {
            method: 'GET',
            headers: {
              'Content-Type': 'application/json',
              'Authorization': `Bearer ${token}`,
              'username' : payload.username
            },
          });

          if (blocksResponse.ok) {
            let savedBlocks = await blocksResponse.json();
            if (!savedBlocks || savedBlocks.length === 0) {
              savedBlocks = [];
            }
            // Se non  un array, lo trasformiamo in un array
            if (!Array.isArray(savedBlocks)) {
                savedBlocks = [savedBlocks]; 
            } else {
              savedBlocks = savedBlocks.map(block => block.toString());
            }
        
            localStorage.setItem(`selectedBlocks-${username}`, JSON.stringify(savedBlocks));
            setSelectedBlocks(savedBlocks); 
          } else {
            console.error('Failed to fetch submitted blocks');
          }

          // Fetch submitted blocks after login
          const submittedResponse = await fetch('https://backend-mover.com/mover/submittedblocks', {
            method: 'GET',
            headers: {
              'Content-Type': 'application/json',
              'Authorization': `Bearer ${token}`,
              'username' : payload.username
            },
          });

          if (submittedResponse.ok) {
            let submittedBlocks = await submittedResponse.json();
            if (!submittedBlocks || submittedBlocks.length === 0) {
              submittedBlocks = [];
            }
            // Se non  un array, lo trasformiamo in un array
            if (!Array.isArray(submittedBlocks)) {
                submittedBlocks = [submittedBlocks]; 
            } else {
                submittedBlocks = submittedBlocks.map(block => block.toString());
            }
        
            localStorage.setItem(`submittedBlocks-${username}`, JSON.stringify(submittedBlocks));
        } else {
            console.error('Failed to fetch submitted blocks');
        }

          navigate('/home');
        } else {
          alert('Login failed');
        }
      } catch (error) {
        console.error('Error logging user:', error);
      }
    }
  };

  const handleRegister = async () => {
    const payload = { username, password };

    try {
      const response = await fetch('https://backend-mover.com/mover/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(payload),
      });

      if (response.ok) {
        alert('Registration successful');
        setIsRegistering(false);
      } else {
        alert('Registration failed');
      }
    } catch (error) {
      console.error('Error registering user:', error);
    }
  };

  return (
    <div className="login-container">
      <h2>{isRegistering ? 'Register' : 'Login'}</h2>
      <input
        type="text"
        placeholder="Username"
        value={username}
        onChange={(e) => setUsername(e.target.value)}
      />
      <input
        type="password"
        placeholder="Password"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
      />
      {isRegistering ? (
        <>
          <button onClick={handleRegister}>Register</button>
          <button onClick={() => setIsRegistering(false)}>Back to Login</button>
        </>
      ) : (
        <>
          <button onClick={handleLogin}>Login</button>
          <button onClick={() => setIsRegistering(true)}>Register</button>
        </>
      )}
    </div>
  );
};

export default Login;