class ApiQueue {
  constructor(concurrency = 3, retryLimit = 3) {
    this.queue = JSON.parse(localStorage.getItem('apiQueue')) || [];
    this.isProcessing = false;
    this.concurrency = concurrency;
    this.retryLimit = retryLimit;
    this.activeRequests = 0;
    this.addPopupsToDOM();
  }

  addPopupsToDOM() {
    const overlay = document.createElement('div');
    overlay.id = 'popup-overlay';
    overlay.className = 'popup-overlay';
    overlay.style.display = 'none';

    const successPopup = document.createElement('div');
    successPopup.id = 'success-popup';
    successPopup.className = 'success-popup';
    successPopup.style.display = 'none';
    successPopup.textContent = 'Invio andato a buon fine!';

    const warningPopup = document.createElement('div');
    warningPopup.id = 'warning-popup';
    warningPopup.className = 'warning-popup';
    warningPopup.style.display = 'none';
    warningPopup.textContent = 'Non chiudere il browser finch non appare il messaggio di invio andato a buon fine.';

    document.body.appendChild(overlay);
    document.body.appendChild(successPopup);
    document.body.appendChild(warningPopup);
  }

  enqueue(request) {
    this.queue.push({ ...request, retries: 0 });
    localStorage.setItem('apiQueue', JSON.stringify(this.queue));
    this.showWarningMessage();
    this.processQueue();
  }

  async processQueue() {
    if (this.isProcessing) return;
    this.isProcessing = true;

    while (this.queue.length > 0 && this.activeRequests < this.concurrency) {
      const request = this.queue.shift();
      this.activeRequests++;
      this.sendRequest(request)
        .then(() => {
          this.activeRequests--;
          localStorage.setItem('apiQueue', JSON.stringify(this.queue));
          this.showSuccessMessage();
        })
        .catch((error) => {
          console.error('Request failed:', error);
          if (request.retries < this.retryLimit) {
            request.retries++;
            this.queue.push(request);
          }
          localStorage.setItem('apiQueue', JSON.stringify(this.queue));
          this.activeRequests--;
        })
        .finally(() => {
          this.processQueue();
        });
    }

    this.isProcessing = false;
  }

  async sendRequest(request) {
    const response = await this.fetchWithTimeout(request.url, request.options, 5000);
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    return response.json();
  }

  fetchWithTimeout(url, options, timeout = 5000) {
    return new Promise((resolve, reject) => {
      const timer = setTimeout(() => reject(new Error('Request timed out')), timeout);
      fetch(url, options)
        .then(response => {
          clearTimeout(timer);
          resolve(response);
        })
        .catch(err => {
          clearTimeout(timer);
          reject(err);
        });
    });
  }

  showSuccessMessage() {
    document.getElementById('success-popup').style.display = 'block';
    document.getElementById('popup-overlay').style.display = 'block';
    setTimeout(() => {
      document.getElementById('success-popup').style.display = 'none';
      document.getElementById('popup-overlay').style.display = 'none';
    }, 3000); // Nasconde il popup dopo 3 secondi
  }

  showWarningMessage() {
    document.getElementById('warning-popup').style.display = 'block';
    document.getElementById('popup-overlay').style.display = 'block';
  }
}

const apiQueue = new ApiQueue();

export const sendApiRequest = (url, options) => {
  apiQueue.enqueue({ url, options });
};
