import React from 'react';
import { NavLink } from 'react-router-dom';
import './Menu.css';

const Menu = () => {
  return (
    <nav className="menu">
      <NavLink to="/map" className={({ isActive }) => "menu-button" + (isActive ? " active" : "")}>Mappa</NavLink>
      <NavLink to="/sections" className={({ isActive }) => "menu-button" + (isActive ? " active" : "")}>Sezioni</NavLink>
      <NavLink to="/program" className={({ isActive }) => "menu-button" + (isActive ? " active" : "")}>Programma</NavLink>
    </nav>
  );
};

export default Menu;
