import React from 'react';
import './Section.css';

const Section = ({ user, section, selectedBlocks, onCheckboxChange, filterColor, isAppAvailable }) => {
  if (!isAppAvailable) {
    return null; // Non mostrare nulla se l'app non  disponibile
  }
  return (
    <div className="section">
      <h3>{section.name}</h3>
      <p>Address: {section.address}</p>
      {section.blocks
        .filter(block => !filterColor || block.color === filterColor)
        .map(block => (
          <div key={block.name} className="block-item">
            <span className={`color-dot ${block.color}`}></span>
            <label>
              <input
                type="checkbox"
                className="custom-checkbox"
                checked={selectedBlocks.includes(block.name)}
                onChange={() => onCheckboxChange(block.name)}
                disabled={
                  localStorage.getItem(`submittedBlocks-${user.username}`)
                      ? JSON.parse(localStorage.getItem(`submittedBlocks-${user.username}`)).includes(block.name)
                      : false
                }
              />
              {block.name}
            </label>
          </div>
        ))}
    </div>
  );
};

export default Section;
