import React, { useState, useEffect } from 'react';
import { useLocation, NavLink, useNavigate } from 'react-router-dom';
import Section from './Section';
import AdminPage from './Admin';
import Modal from 'react-modal';
import './SectionList.css';
import { sectionsData } from '../sectionsData';
import { appConfig } from '../config';
import { users } from '../users';

const SectionList = ({ user, selectedBlocks, onCheckboxChange, onSubmit, showWarning, timeRemaining, responsesSent }) => {
  const location = useLocation();
  const navigate = useNavigate();

  const isAdmin = user.username === 'mover-admin' && 
                  users.some(u => u.username === user.username && u.password === 'M0v3r4pp!');

  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [filterColor, setFilterColor] = useState('');
  const [showSuccessPopup, setShowSuccessPopup] = useState(false);
  const [showErrorPopup, setShowErrorPopup] = useState(false);
  const [participating, setParticipating] = useState(false);
  const [isAppAvailable, setIsAppAvailable] = useState(true);
  const [lastWarningTime, setLastWarningTime] = useState(null);
  const [submittedBlocks, setSubmittedBlocks] = useState([]);

  useEffect(() => {
    const storedBlocks = localStorage.getItem(`submittedBlocks-${user.username}`);
    if (storedBlocks) {
      setSubmittedBlocks(JSON.parse(storedBlocks));
    }
  }, [user.username]);

  useEffect(() => {
    const checkAvailability = async () => {
      const now = new Date();
      const timeDiff = appConfig.deadline - now;
      if (timeDiff <= 0) {
        // if (!responsesSent) {
        //   await handleSave();
        // }
        handleSave();
        setIsAppAvailable(false);
      } 
    };
  
    // Esegui il controllo IMMEDIATAMENTE al primo render
    checkAvailability();
  
    // Esegui il controllo ogni secondo per aggiornamenti successivi
    const interval = setInterval(checkAvailability, 1000);
  
    return () => clearInterval(interval);
  }, [lastWarningTime, responsesSent]);

  useEffect(() => {
    if (location.state?.sectionId) {
      const sectionElement = document.getElementById(`section-${location.state.sectionId}`);
      if (sectionElement) {
        sectionElement.scrollIntoView({ behavior: 'smooth' });
      }
    }
  }, [location]);

  const handleOpenModal = () => {
    setModalIsOpen(true);
  };

  const handleConfirmSubmit = async () => {
    const storedBlocks = localStorage.getItem(`selectedBlocks-${user.username}`);
    const submittedBlocksStorage = localStorage.getItem(`submittedBlocks-${user.username}`);
  
    let existingSubmittedBlocks = submittedBlocksStorage ? JSON.parse(submittedBlocksStorage) : [];
  
    if (storedBlocks) {
      const newSubmittedBlockNames = JSON.parse(storedBlocks);
  
      // Merge the new blocks with the existing ones, avoiding duplicates
      const updatedSubmittedBlocks = Array.from(new Set([...existingSubmittedBlocks, ...newSubmittedBlockNames]));
  
      setSubmittedBlocks(updatedSubmittedBlocks); // Update the state
  
      // Save the updated blocks in localStorage
      localStorage.setItem(`submittedBlocks-${user.username}`, JSON.stringify(updatedSubmittedBlocks));
    }
    await handleSave();
    await onSubmit();
    setModalIsOpen(false);
  };

  const handleCloseModal = () => {
    setModalIsOpen(false);
  };

  const handleFilterChange = (e) => {
    setFilterColor(e.target.value);
  };

  const handleSave = async () => {
    const localStorageKey = `selectedBlocks-${user.username}`;
    const savedBlocks = localStorage.getItem(localStorageKey);
  
    if (savedBlocks) {
      const token = localStorage.getItem('token');
      try {
        const response = await fetch('https://backend-mover.com/mover/save', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`,
          },
          body: JSON.stringify({ username: user.username, selectedBlocks }),
        });
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        setShowSuccessPopup(true);
        setTimeout(() => setShowSuccessPopup(false), 3000); // Hide the popup after 3 seconds
      } catch (error) {
        console.error('There was a problem with the fetch operation:', error);
        setShowErrorPopup(true);
        setTimeout(() => setShowErrorPopup(false), 3000); // Hide the popup after 3 seconds
      }
      return;
    }
  };

  return (
    <div className="section-list">
      {isAppAvailable ? (
        <>
          <h2>Sezioni
            <div className="filter-container">
              <label>Grado:</label>
              <select value={filterColor} onChange={handleFilterChange}>
                <option value="">Tutti</option>
                <option value="red">Rosso</option>
                <option value="green">Verde</option>
                <option value="blue">Blu</option>
              </select>
            </div>
          </h2>
          {sectionsData.map(section => (
            <div id={`section-${section.id}`} key={section.id}>
              <Section
                section={section}
                selectedBlocks={selectedBlocks}
                onCheckboxChange={onCheckboxChange}
                filterColor={filterColor}
                isAppAvailable={isAppAvailable}
                submittedBlocks={submittedBlocks}
                user={user}
              />
            </div>
          ))}
          {showSuccessPopup && (
            <div className="success-popup">
              Salvataggio avvenuto con successo!
            </div>
          )}
          <div className="participate-checkbox">
              <input
                type="checkbox"
                id="participate"
                checked={participating}
                onChange={(e) => setParticipating(e.target.checked)}
                className='custom-checkbox'
              />
              <label htmlFor="participate">Partecipo alla gara</label>
          </div>
          {showErrorPopup && (
            <div className="error-popup">
              Errore durante il salvataggio. Riprova.
            </div>
          )}
          <div className="submit-button-container">
            <button className="btn-save" onClick={handleSave}>Salva</button>
            {participating && (
              <button className="btn-submit" onClick={handleOpenModal}>Invia blocchi</button>
            )}
          </div>
          <div className="footer">
            <NavLink to="/regolamento" className="link">Regolamento</NavLink>
            <NavLink to="/policy" className="link">Policy</NavLink>
          </div>
          {isAdmin && (
            <button className="admin-button" onClick={() => navigate('/adminpage')}>
              Admin
            </button>
          )}
          <Modal
            isOpen={modalIsOpen}
            onRequestClose={handleCloseModal}
            contentLabel="Confirm Submit"
            className="modal"
            overlayClassName="overlay"
          >
            <h2>Sei sicuro di inviare le sezioni/blocchi?</h2>
            <h2>Dopo aver cliccato S non sar pi possibile modificare i blocchi inviati.</h2>
            <button onClick={handleConfirmSubmit}>S</button>
            <button onClick={handleCloseModal}>No</button>
          </Modal>
        </>
      ) : (
        <div className="race-finished">
          <h2>La gara  finita!</h2>
          <h3>I tuoi blocchi sono stati inviati automaticamente</h3>
          <p>Grazie per aver partecipato!</p>
          <NavLink to="/results" className="link">Visualizza Classifica</NavLink>
          {isAdmin && (
            <button className="admin-button" onClick={() => navigate('/adminpage')}>
              Admin
            </button>
          )}
          <div className="footer">
            <NavLink to="/regolamento" className="link">Regolamento</NavLink>
            <NavLink to="/policy" className="link">Policy</NavLink>
          </div>
        </div>
      )}
    </div>
  );
};

export default SectionList;