import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Results from './Results';
import './Admin.css';
import { users } from '../users';

const AdminPage = (user) => {
  const navigate = useNavigate();

  // useEffect(() => {
  //   if (!user || user.username !== 'mover-admin' || !users.some(u => u.username === user.username && u.password === 'M0v3r4pp!')) {
  //     navigate('/'); // Reindirizza alla homepage se non  admin
  //   }
  // }, [user, navigate]);

  const [eventData, setEventData] = useState({
    title: '',
    time: '',
    description: '',
    address: '',
    position: ['', ''],
    start: '',
    end: '',
    iconUrl: '',
    backgroundColor: '',
  });

  const [deleteTitle, setDeleteTitle] = useState('');
  const [displayScores, setDisplayScores] = useState([]); // Quelli visibili in Results
  const [displayScoresResults, setDisplayScoresResults] = useState([]); // Stato per i punteggi da mostrare in Results

  // Funzione per ottenere i punteggi dal backend
  const fetchScores = async () => {
    try {
      const response = await fetch('https://backend-mover.com/mover/scores');
      if (!response.ok) throw new Error('Errore nel recupero punteggi');

      const data = await response.json();
      setDisplayScores(data.sort((a, b) => b.finalScore - a.finalScore)); // Ordina i punteggi
    } catch (error) {
      console.error('Errore:', error);
      alert('Errore nel recupero punteggi');
    }
  };

  // Funzione per inviare i punteggi a Results
  const sendScoresToResults = () => {
    setDisplayScoresResults(displayScores); // Aggiorna lo stato con i punteggi da mostrare
    alert('Punteggi inviati alla pagina Results!');
    if (navigator.serviceWorker) {
      navigator.serviceWorker.ready.then(registration => {
        registration.showNotification("Classifica aggiornata!", {
          body: "La classifica  disponibile!",
          icon: "/logo192.png",
          tag: "score-update" // Tag per evitare notifiche duplicate
        });
      });
    }
  };


  const handleChange = (e) => {
    const { name, value } = e.target;
    setEventData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handlePositionChange = (e, index) => {
    const newPos = [...eventData.position];
    newPos[index] = e.target.value;
    setEventData((prevData) => ({ ...prevData, position: newPos }));
  };

  const validateFields = (isUpdate = false) => {
    if (!eventData.title) {
      alert('Il titolo  obbligatorio');
      return false;
    }

    if (!isUpdate) {
      const requiredFields = ['time', 'description', 'address', 'start', 'end'];
      for (const field of requiredFields) {
        if (!eventData[field]) {
          alert(`Il campo ${field}  obbligatorio`);
          return false;
        }
      }

      if (!eventData.position[0] || !eventData.position[1]) {
        alert('Latitudine e longitudine sono obbligatorie');
        return false;
      }
    }

    return true;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!validateFields()) return;
  
    await sendRequest('POST', 'Evento aggiunto con successo!');
  };
  
  const handleUpdate = async (e) => {
    e.preventDefault();
    if (!validateFields(true)) return;
  
    await sendRequest('PUT', 'Evento aggiornato con successo!');
  };

  const sendRequest = async (method, successMessage) => {
    const formattedData = {
      id: Date.now(),
      title: eventData.title,
      ...(eventData.time && { time: eventData.time }),
      ...(eventData.description && { description: eventData.description }),
      ...(eventData.address && { address: eventData.address }),
      ...(eventData.position[0] && eventData.position[1] && {
        position: [parseFloat(eventData.position[0]), parseFloat(eventData.position[1])],
      }),
      ...(eventData.start && { start: new Date(eventData.start) }),
      ...(eventData.end && { end: new Date(eventData.end) }),
      ...(eventData.iconUrl && { iconUrl: eventData.iconUrl }),
      ...(eventData.backgroundColor && { backgroundColor: eventData.backgroundColor }),
    };
  
    try {
      const response = await fetch(`https://backend-mover.com/mover/events`, {
        method: method,
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formattedData),
      });
  
      if (!response.ok) throw new Error('Errore nella richiesta');
  
      alert(successMessage);
      setEventData({
        title: '',
        time: '',
        description: '',
        address: '',
        position: ['', ''],
        start: '',
        end: '',
        iconUrl: '',
        backgroundColor: '',
      });
  
    } catch (error) {
      console.error('Errore:', error);
      alert('Errore nella richiesta');
    }
  };

  const handleDelete = async () => {
    if (!deleteTitle) return alert('Inserisci un nome evento');

    try {
      const response = await fetch(`https://backend-mover.com/mover/events`, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ title: deleteTitle }),
      });

      if (!response.ok) throw new Error('Errore nella richiesta');

      alert('Evento eliminato con successo!');
      setDeleteTitle('');

    } catch (error) {
      console.error('Errore:', error);
      alert('Errore nella richiesta');
    }
  };

  const handleCalculate = async () => {
    try {
      const response = await fetch(`https://backend-mover.com/mover/points`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
      });

      if (!response.ok) throw new Error('Errore nel calcolo dei punti');

      alert('Calcolo avviato con successo!');

    } catch (error) {
      console.error('Errore:', error);
      alert('Errore durante il calcolo');
    }
  };

  return (
    <div className="admin-container">
      <h1 className="admin-title">Pannello di Amministrazione</h1>

      {/* Form per Aggiungere/Modificare Eventi */}
      <div className="event-form">
        <h2>Gestisci Evento</h2>
        <form onSubmit={handleSubmit}>
          <input type="text" name="title" placeholder="Titolo (Obbligatorio)" value={eventData.title} onChange={handleChange} required />
          <input type="text" name="time" placeholder="Orario (es. 11:00 AM)" value={eventData.time} onChange={handleChange} />
          <textarea name="description" placeholder="Descrizione" value={eventData.description} onChange={handleChange} />
          <input type="text" name="address" placeholder="Indirizzo" value={eventData.address} onChange={handleChange} />

          <div className="position-inputs">
            <input type="number" step="any" placeholder="Latitudine" value={eventData.position[0]} onChange={(e) => handlePositionChange(e, 0)} />
            <input type="number" step="any" placeholder="Longitudine" value={eventData.position[1]} onChange={(e) => handlePositionChange(e, 1)} />
          </div>

          <input type="datetime-local" name="start" value={eventData.start} onChange={handleChange} />
          <input type="datetime-local" name="end" value={eventData.end} onChange={handleChange} />

          <input type="text" name="iconUrl" placeholder="URL Icona" value={eventData.iconUrl} onChange={handleChange} />
          <input type="text" name="backgroundColor" placeholder="Colore di sfondo (es. red, #ff0000)" value={eventData.backgroundColor} onChange={handleChange} />

          <div className="button-group">
            <button type="submit" className="submit-button">Aggiungi Evento</button>
            <button type="button" className="update-button" onClick={handleUpdate}>Aggiorna Evento</button>
          </div>
        </form>
      </div>

      {/* Sezione per Eliminare Eventi */}
      <div className="delete-section">
        <h2>Elimina Evento</h2>
        <input type="text" placeholder="Nome evento da eliminare" value={deleteTitle} onChange={(e) => setDeleteTitle(e.target.value)} />
        <button className="delete-button" onClick={handleDelete}>Elimina</button>
      </div>

      {/* Sezione per il Calcolo */}
      <div className="calculation-section">
        <h2>Calcolo</h2>
        <button className="calculate-button" onClick={handleCalculate}>Avvia Calcolo</button>
      </div>


      <div className="scores-section">
        <h2>Punteggi</h2>
        <div className="button-group">
          <button type="button" className="refresh-button" onClick={fetchScores}>Ottieni Punteggi</button>
          <button type="button" className="update-button-score" onClick={sendScoresToResults}>Invia Punteggi</button>
        </div>
        <h2>Temporanei</h2>
        <ul>
          {displayScores.map((result, index) => (
            <li key={index}>
              {index + 1}. {result.username}: {result.finalScore}
            </li>
          ))}
        </ul>
      
        <h2>Pagina pubblica</h2>
        {/* Passiamo i punteggi a Results */}
        <Results scores={displayScoresResults} setScores={setDisplayScoresResults} />
      </div>
      </div>
  );
};

export default AdminPage;
