import React from 'react';
import './Results.css';

const Results = ({ scores = [], setScores }) => { // Aggiunto setScores come prop
  return (
    <div className="results-container">
      <h2>Risultati</h2>
      {scores.length === 0 ? (
        <p className="no-scores">Punteggi in fase di calcolo!</p>
      ) : (
        <ul>
          {scores.map((result, index) => (
            <li key={index}>
              {index + 1}. {result.username}: {result.score}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

export default Results;
